import { ADD_ELE, TOGGLE_ELE, SET_STATE , DELETE_ELE} from "../actionTypes";

const initialState = {
    elements: []
}

const storeX = (state = initialState, action) => {

    switch (action.type) {

        case SET_STATE:
            return state;

        case ADD_ELE:
            console.log('got + ' + action.payload)
            return {
                ...state,
                elements: [...state.elements, action.payload]
            };

        case DELETE_ELE:
           // console.log(`want to delete ${action.payload.id}`)
            return {
                ...state,
                elements: state.elements.filter((item, index) => { return +action.payload.id !== item.id})
            };


        default:
            return state;
    };

}

    export default storeX;