import { ADD_ELE, TOGGLE_ELE, SET_FILTER, SET_STATE, DELETE_ELE, AUTH_USER } from "./actionTypes";

let current = 0;

 const addEle =  (content) => ({
    type: ADD_ELE,
    payload: {
        id: ++current,
        content
    }
});

 const authUser = (uId) => ({
     type: AUTH_USER,
     payload: {uId}
 });

 const setState = () => ({
    type: SET_STATE
});

 const deleteEle = (id) => ({
     type: DELETE_ELE,
     payload: { id }
 });

 const toggleEle = (id) => ({
    type: TOGGLE_ELE,
    payload: { id }
});

 const setFilter = (filter) => ({
    type: SET_FILTER, payload: { filter }
});

export default  {
    authUser,
    addEle,
    setFilter,
    setState,
    toggleEle,
    deleteEle
}